import React, { Component } from "react";
import "./Todo.css";

class Todo extends Component {
  constructor(props) {
    super(props);
    this.state = { isEditing: false, task: this.props.item };
    this.handleDelete = this.handleDelete.bind(this);
    this.toggleEditMode = this.toggleEditMode.bind(this);
    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.toggleComplete = this.toggleComplete.bind(this);
  }
  toggleComplete(evt) {
    evt.preventDefault();
    this.props.toggleIsComplete(this.props.isComplete, this.props.id);
  }
  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value,
    });
  }
  handleUpdate(evt) {
    evt.preventDefault();
    this.props.updateTodo(this.props.id, this.state.task);
    this.setState({ isEditing: false });
  }
  toggleEditMode(evt) {
    evt.preventDefault();

    this.setState((currState) => ({
      isEditing: !currState.isEditing,
    }));
  }
  handleDelete(event) {
    event.preventDefault();
    this.props.deleteTodo(this.props.id);
  }
  render() {
    let result;
    if (this.state.isEditing) {
      result = (
        <div>
          <form onSubmit={this.handleUpdate}>
            <input
              type="text"
              value={this.state.task}
              name="task"
              onChange={this.handleChange}
            />
            <button onClick={this.handleSubmit}>submit changes</button>
          </form>
        </div>
      );
    } else {
      result = (
        <div className="todo-container">
          <form>
            <div className="todo">
              {this.props.isComplete ? (
                <i
                  style={{ marginRight: "30px" }}
                  className="fal fa-check-circle"
                  aria-hidden="true"
                ></i>
              ) : (
                ""
              )}
              <h5
                style={{ marginRight: "40px" }}
                className={this.props.isComplete ? "crossed-line" : ""}
              >
                {this.props.item}{" "}
              </h5>
              <button style={{ margin: "5px" }} onClick={this.handleDelete}>
                {<i className="fa fa-trash" aria-hidden="true"></i>}
              </button>
              <button style={{ margin: "5px" }} onClick={this.toggleEditMode}>
                {<i className="fa fa-edit" aria-hidden="true"></i>}
              </button>
              <button onClick={this.toggleComplete} style={{ margin: "5px" }}>
                {<i className="fas fa-check"></i>}
              </button>
            </div>
          </form>
        </div>
      );
    }
    return <div>{result}</div>;
  }
}
export default Todo;



