import React, { Component } from "react";
import CreateTodoForm from "./CreateTodoForm";
import Todo from "./Todo";

class Todolist extends Component {
  constructor(props) {
    super(props);
    this.state = {
      todos: [],
    };
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.updateTodo = this.updateTodo.bind(this);
    this.toggleIsComplete = this.toggleIsComplete.bind(this);
  }
  toggleIsComplete(isComplete, id) {
    console.log(isComplete, id);
    const updatedTodos = this.state.todos.map((t) => {
      if (t.id === id) {
        return { ...t, completed: !isComplete };
      }
      return t;
    });
    this.setState({ todos: updatedTodos });
  }
  // the method below is actually invoked in the child component(createTodoForm)
  // the object passed in as "item" is generated in the CONTROLLED FORM below.
  // CRITICAL TO UNDERSTAND!!!
  // The CONTROLLED FORM takes , at the time of submit event WHATEVER is in state.
  // you can then reset the state to empty
  // The form state is temporary - it only exists temporarily to invoke addItem with
  // the correct function and arguments
  addItem(item) {
    // below line is !important! what is adding on an id to the item coming up from the component below
    console.log(item);
    this.setState((currentState) => ({
      todos: [...currentState.todos, item],
    }));
  }
  deleteItem(id) {
    this.setState({ todos: this.state.todos.filter((todo) => todo.id !== id) });
  }
  updateTodo(id, updatedTask) {
    const updatedTodos = this.state.todos.map((t) => {
      if (t.id === id) {
        return { ...t, todo: updatedTask };
      }
      return t;
    });
    console.log(updatedTodos);
    this.setState({ todos: updatedTodos });
  }
  render() {
    return (
      <div>
        <h1>To-do List</h1>
        <CreateTodoForm addItem={this.addItem} />
        <div>
          {this.state.todos.map((todo) => (
            <div key={todo.id}>
              <Todo
                deleteTodo={this.deleteItem}
                id={todo.id}
                key={todo.id}
                isComplete={todo.completed}
                toggleIsComplete={this.toggleIsComplete}
                item={todo.todo}
                updateTodo={this.updateTodo}
              />
            </div>
          ))}
        </div>
      </div>
    );
  }
}
export default Todolist;



